// 校验人员姓名是否合法
function isUsername(name) {
  let reg = /^[\u4e00-\u9fa5]{2,4}$/;
  return reg.test(name);
}

// 校验身份证是否合法
function isIdCard(idCard) {
  let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
  if (reg.test(idCard) === false) {
    return false;
  } else {
    var xishu = [7,9,10,5,8,4,2,1,6,3,7,9,10,5,8,4,2];
    var sum = 0;
    for(var i=0; i<xishu.length; i++){
      sum += parseInt(idCard.charAt(i)) * xishu[i];
    }
    var yushu = sum%11;
    var ch = ['1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2'];
    var code = ch[yushu];
    var last = idCard.charAt(17);
    last = last=='x' ? 'X': last;
    return  last == code;
  }
}

// 校验手机号码是否合法
function isMobile(str) {
  const reg = /^1[3-9]\d{9}$/;
  return reg.test(str);
}

//校验手机号和座机
function checkTel(){
    var isPhone = /^[0][1-9]{2,3}-[0-9]{5,10}$/;
    var isMob=/^((\+?86)|(\(\+86\)))?(13[012356789][0-9]{8}|15[012356789][0-9]{8}|18[02356789][0-9]{8}|147[0-9]{8}|1349[0-9]{7})$/;
    var value=document.getElementById("telephoneCon").value.trim();
    if(isMob.test(value)||isPhone.test(value)){
        return true;
    }else{
        return false;
    }
  }


// 校验邮箱是否合法
function isEmail(str) {
  const reg = /^[A-Za-z0-9\u4e00-\u9fa5_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
  return reg.test(str);
}

// 校验输入值是否都为中文
function isChinese(input) {
  let reg = /^[\u4e00-\u9fa5]+$/;
  return reg.test(input);
}

// 校验输入值是否都为数字
function isNumber(input) {
  let reg = /^\d+$/;
  return reg.test(input);
}

// 校验银行卡


export default {
  isUsername, isIdCard, isMobile, isEmail, isChinese, isNumber, checkTel
}


