import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

const HomePage = resolve => require(['@/pages/homePage/home.vue'], resolve);
const test1 = resolve => require(['@/pages/homePage/test1.vue'], resolve);
const test2 = resolve => require(['@/pages/homePage/test2.vue'], resolve);
const test3 = resolve => require(['@/pages/homePage/test3.vue'], resolve);
const img = resolve => require(['@/pages/homePage/img.vue'], resolve);


export default new Router({
  routes: [
    {path: '/',component: HomePage,name:'homePage',meta:{title:'测试摄像头',sign:'homePage'}},    
    {path: '/test1',component: test1,name:'test1',meta:{title:'测试摄像头1',sign:'test1'}},    
    {path: '/test2',component: test2,name:'test2',meta:{title:'测试摄像头2',sign:'test2'}},    
    {path: '/test3',component: test3,name:'test3',meta:{title:'测试摄像头3',sign:'test3'}},    
    {path: '/img',component: img,name:'img',meta:{title:'照片',sign:'img'}},    
  ]
})


