// 事项
let transforHandleItem = status =>{
  if (status) {
    var ret = status;
    switch (status) {
      case '1':
        ret = '参保证明打印';
        break;
      case '2':
        ret = '灵活就业人员参保登记';
        break;
      case '3':
        ret = '灵活就业人员停保登记';
        break;
      case '4':
        ret = '企业职工养老关系接续';
        break;
      case '5':
        ret = '企业职工养老关系转移';
        break;
      case '6':
        ret = '养老保障待遇领取资格认证';
        break;
      case '7':
        ret = '退休一件事联办';
        break;
      case '8':
        ret = '灵活就业人员社保补贴';
        break;
      case '9':
        ret = '社会保险职工参保信息变更登记';
        break;
    }
    return ret
  } else {
    return status;
  }
}

// 市民卡状态
let transforCityCardStatus = status =>{
  if (status) {
    var ret = status;
    switch (status) {
      case '0':
        ret = '封存';
        break;
      case '1':
        ret = '正常';
        break;
      case '2':
        ret = '挂失';
        break;
      case '3':
        ret = '应用锁定';
        break;
      case '4':
        ret = '临时挂失';
        break;
      case '9':
        ret = '注销';
        break;
    }
    return ret
  } else {
    return '无';
  }
}

