function setBasicInfo() {
  let areaCode = paramStr("areacode");
  let date = new Date();
  let year = date.getFullYear();
  let month = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
  sessionStorage.setItem("currentDate", `${year}${month}`);
  if (areaCode) {
    console.log("set areaCode")
    sessionStorage.setItem("areaCode", Base64.encode(areaCode));
  }
}

// 解密，获取用户明文信息
function decode(cid, phone, cname) {
  let data = {
    idCard: cid,
    mobile: phone,
    mwname: cname
  }
  return new Promise((resolve, reject) => {
    axios.post("https://znfw.wzhrss.gov.cn/ifs/vip/decode", "\"" + Base64.encode(JSON.stringify(data)) + "\"").then(res => {
      resolve(res);
      res = eval("(" + Base64.decode(res) + ")");
      console.log(res)
      let age = caculateAge(getBirthDate(res.data.idCard));
      sessionStorage.setItem("age", age);
      sessionStorage.setItem("mwphone", Base64.encode(res.data.mobile));
      sessionStorage.setItem("mwname", Base64.encode(res.data.mwname));
      judgeGender(res.data.idCard);
    }).catch((err) => {
      console.log(err);
      Dialog.alert({ message: "用户信息解密失败，请重试" });
      reject(err)
    })
  })
}
// 判断用户性别
function judgeGender(idCard) {
  if (parseInt(idCard.substr(16, 1)) % 2 == 1) {
    // 男
    sessionStorage.setItem("gender", "男");
  } else {
    // 女
    sessionStorage.setItem("gender", "女");
  }
}
// 获取用户信息
function getUserInfo(cid) {
  let data = {
    orgno: 'wmp1001',
    aae135: cid,
    aaa027: Base64.decode(sessionStorage.getItem("areaCode")),
    aae002: sessionStorage.getItem("currentDate"),
  }
  console.log(data)
  return new Promise((resolve, reject) => {
    axios.post("/handle/pysb/wmp1001", "\"" + Base64.encode(JSON.stringify(data)) + "\"").then(res => {
      resolve(res)
      res = eval("(" + Base64.decode(res) + ")");
      console.log(res)
      if (res.status == '0') {
        if (res.data.aae135) {
          sessionStorage.setItem("userInfo", JSON.stringify(res.data));
        } else {
          sessionStorage.setItem("userInfo", '')
        }
      } else {
        sessionStorage.setItem("userInfo", '')
      }
    }).catch((err) => {
      console.log(err);
      Dialog.alert({ message: "获取用户信息失败，请重试" });
      sessionStorage.setItem("userInfo", '')
      reject(err);
    })
  })
}
// 截取身份证上出生日期
function getBirthDate(idCard) {
  let birthday;
  if (idCard.length == 15) {
    birthday = "19" + idCard.substr(6, 6);
  } else if (idCard.length == 18) {
    birthday = idCard.substr(6, 8);
  }
  return birthday.replace(/(.{4})(.{2})/, "$1-$2-");
}
// 计算年龄
function caculateAge(birthDate) {
  var birArr = birthDate.split("-");
  var birYear = birArr[0];
  var birMonth = birArr[1];
  var birDay = birArr[2];

  d = new Date();
  var nowYear = d.getFullYear();
  var nowMonth = d.getMonth() + 1;
  var nowDay = d.getDate();
  var returnAge;

  if (birArr == null) {
    return false
  };
  var d = new Date(birYear, birMonth - 1, birDay);
  if (d.getFullYear() == birYear && (d.getMonth() + 1) == birMonth && d.getDate() == birDay) {
    if (nowYear == birYear) {
      returnAge = 0;
    } else {
      var ageDiff = nowYear - birYear;
      if (ageDiff > 0) {
        if (nowMonth == birMonth) {
          var dayDiff = nowDay - birDay;
          if (dayDiff < 0) {
            returnAge = ageDiff - 1;
          } else {
            returnAge = ageDiff;
          }
        } else {
          var monthDiff = nowMonth - birMonth;
          if (monthDiff < 0) {
            returnAge = ageDiff - 1;
          } else {
            returnAge = ageDiff;
          }
        }
      } else {
        return 0;
      }
    }
    return returnAge;
  } else {
    return 0;
  }
}
